import { Component, OnInit } from '@angular/core';
import { NavController, PopoverController } from '@ionic/angular';

@Component({
  selector: 'app-close-session',
  templateUrl: './close-session.page.html',
  styleUrls: ['./close-session.page.scss'],
})
export class CloseSessionPage implements OnInit {
  darkMode: any = false;
  constructor(
    private navCtrl: NavController,
    private popoverCtrl: PopoverController
  ) {
    if (localStorage.getItem('dark') == '1') this.darkMode = true;
  }

  ngOnInit() {}

  Exit() {
    this.popoverCtrl.dismiss();
    localStorage.clear();
    this.navCtrl.navigateBack('login');
  }

  ToggleDark() {
    this.darkMode = !this.darkMode;
    document.getElementById('cabeceras').classList.toggle('dark');
    document.getElementById('s-menu').classList.toggle('dark');
    if (this.darkMode) {
      localStorage.setItem('dark', '1');
    } else {
      localStorage.setItem('dark', '0');
    }
  }
}
